






























import {Vue, Component} from 'vue-property-decorator';
import Open, {OpenInterface} from "@/components/Open.vue";
import {Form} from "element-ui";
import {trace_web} from '@/api'
import _ from 'lodash';
import SelectFile from '@/components/SelectFile.vue'

@Component({
    components: {
        Open,
        SelectFile
    }
})
export default class EditOpen extends Vue implements OpenInterface {
    $refs!: {
        open: Open,
        formValidate: Form
    }
    fileList: any = []
    formData: any = {};
    rules: any = {
        name: [
            {required: true, message: '请输入文件名称', trigger: 'blur'},
        ],
        fileList: [
            {
                required: true,
                validator: this.validateFile
            },
            {
                required: true,
                validator: this.validateSize
            }
        ]
    }

    get title() {
        return this.formData.id ? '编辑政策' : '新增政策';
    }

    get isOverSize() {
        return this.fileList.every((el: any) => {
            return (el.size / 1024 / 1024) >= 50;
        })
    }

    validateFile(rule: any, value: string, callback: any) {
        if (this.fileList.length === 0) {
            callback(new Error('请上传附件'));
        } else if (this.fileList.length > 1) {
            callback(new Error('只能上传一个文件'));
        } else {
            callback();
        }
    }

    validateSize(rule: any, value: string, callback: any) {
        if (this.isOverSize) {
            callback(new Error('文件最大不能超过50M'));
        } else {
            callback();
        }
    }

    // 获取文件后缀
    getSuffix(name: string) {
        let arr = name.split('.');
        let suffix = _.last(arr);
        return `.${suffix}`;
    }

    outFiles(files: any) {
        if (files) {
            this.fileList.push(files);
            if (!this.formData.name) {
                this.$set(this.formData, 'name', files.name);
            }
            this.$set(this.formData, 'suffix', this.getSuffix(files.name));
            this.$refs.formValidate.validate(() => {
            });
        }
    }

    save() {
        this.$refs.formValidate.validate((valid: any) => {
            if (valid) {
                this.upLoadFile();
            }
        })
    }

    async upLoadFile() {
        let res: any = {};
        // 类型是file时上传文件
        if (this.fileList[0] instanceof File) {
            res = await trace_web.trace_web_file_uploadFile_post({
                data: {
                    file: this.fileList[0],
                    fileType: 'policy',
                    modulType: 'policy'
                },
                load: {fullLoading: true},
                isUpload: true
            })
        }
        let params = {
            address: res.message,
            ...this.formData,
        }
        // 更新
        if (this.formData.id) {
            await trace_web.trace_web_policy_update_post({
                data: {
                    ...params
                }
            })
        } else {
            // 保存
            await trace_web.trace_web_policy_save_post({
                data: {
                    ...params
                }
            })
        }

        this.$notify({
            title: '成功',
            message: '操作成功！',
            type: 'success'
        });
        this.$emit('on-close');
        this.$refs.open.close();
    }

    removeFile(item: any, index: number) {
        this.fileList.splice(index, 1);
        this.$refs.formValidate.validate(() => {
        })
    }

    open(row?: any) {
        this.$refs.open.open();
        if (row) {
            let {id, address, name, suffix} = row;
            this.formData = {
                id: id,
                address: address,
                name: name,
                suffix: suffix
            }
            this.fileList.push(row);
        }
    }
}
