


























/**
 * 基础弹窗组件
 */
import {Component, Vue, Prop} from 'vue-property-decorator';


@Component({
    name: 'Open'
})
export default class Open extends Vue {
    @Prop({
        type: String,
        default: '确定'
    })
    saveText!: string;
    @Prop({
        type: String,
        default: '取消'
    })
    closeText!: string;
    @Prop({
        type: Boolean,
        default: false
    })
    hideSave!: boolean;
    @Prop({
        type: Boolean,
        default: false
    })
    hideClose!: boolean;
    @Prop({
        type: Boolean,
        default: false
    })
    hideFoot!: boolean;
    @Prop({
        type: String,
        default: '信息'
    })
    title!: string;
    @Prop({
        type: [Number, String],
        default: 410
    })
    width!: number | string;
    @Prop({
        type: String
    })
    className!: string;
    @Prop({
        type: Boolean,
        default: true
    })
    showClose!: boolean;
    @Prop({
        type: Boolean,
        default: false
    })
    closeModal!: boolean;

    modalShow: boolean = false;
    top: any = '15vh'

    save() {
        this.$emit('on-save');
    }

    close() {
        this.$emit('on-close');
        this.modalShow = false;
        window.removeEventListener("resize", this.dialogPosition)
    }

    dialogPosition() {
        if (document.body.clientHeight <= 600) {
            this.top = '2vh'
        } else if (document.body.clientHeight > 600 && document.body.clientHeight <= 700) {
            this.top = '10vh'
        } else {
            this.top = '15vh'
        }
    }

    open() {
        //初始化父组件中的data值
        //需要将this.$parent.$options.data函数的this指向this.$parent再执行 保证函数中的this指向正常
        //修复表单校验自定义函数报错的问题
        //@ts-ignore
        Object.assign(this.$parent.$data, this.$parent.$options.data.bind(this.$parent)());

        //修复验证信息直接提示的问题
        this.modalShow = false;
        this.$nextTick(() => {
            //打开弹窗之前显示弹窗
            this.modalShow = true;
            this.$nextTick(() => {
                //@ts-ignore
                this.isOpen = true;
                this.dialogPosition()
                window.addEventListener("resize", this.dialogPosition)
            });
        });
    }
}

interface OpenInterface {
    open: (...params: any[]) => any
}

export {
    OpenInterface
}
